.forHim,
.bestSellerProducts,
.topBackground {
  width: 100%;
  height: 80vh;
  object-fit: cover;
  object-position: bottom;
  display: block;
}

.coupon {
  text-transform: uppercase;
}

.couponRow {
  display: flex;
  flex: 1 1 100%;
}
.couponRowLeft {
  background-color: black;
  color: white;
  flex: 0 0 75%;
  max-width: 75%;
  padding-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.couponRowRight {
  background-color: #f1c50e;
  color: black;

  flex: 0 0 25%;
  max-width: 25%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.karusel {
  position: relative;
}
.left,
.right {
  width: 4rem;
  height: 4rem;
  border-radius: 100px;
  border-color: transparent;
  background-color: white;

  font-size: 2rem;
  position: absolute;
}

.left:link,
.left:visited,
.right:link,
.right:visited {
  color: white;
}
.left:hover,
.left:active,
.right:hover,
.right:active {
  color: blue;
  background-color: grey;
}
.left {
  left: 4rem;
  top: 50%;
  transform: translateY(-50%);
}
.right {
  right: 4rem;
  top: 50%;
  transform: translateY(-50%);
}

.forHim {
  object-position: 0rem 35%;
}

.trycss2 {
  height: 200px;
  width: 100px;
  border: 2px solid blue;
}
