.searchBar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10rem;
  background-color: transparent;
}
.icon {
  width: 30rem;
}
.searchBox {
  width: 30rem;
  margin-top: 110px;
}
.inputBox {
  border: 2px solid #000;
  width: 70%;
  padding: 12px 20px;
  font-size: large;
}
.submit {
  border: 2px solid #000;
  width: 20%;
  padding: 12px 20px;
  background-color: #22b14c;
  color: white;
  font-size: large;
}
.submit:link,
.submit:visited {
  color: white;
}
.submit:hover,
.submit:active {
  color: blue;
}
