.footerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #c3c3c3;
  color: white;
}

.footerContainer h1 {
  font-size: large;
}

.footerContainer ul {
  list-style: none;
}

.listItem {
  margin-left: -2rem;
  padding-bottom: 0.5rem;
  text-decoration: none;
}

.listItem:link,
.listItem:visited {
  color: white;
}
.listItem:hover,
.listItem:active {
  color: blue;
}
