.topbar {
  display: flex;
  background-color: #22b14c;
  font-size: large;
  color: white;
  justify-content: right;
  align-items: center;
  gap: 2rem;
  padding-right: 3rem;
}

.register {
  text-decoration: none;
}

.register:link,
.register:visited {
  color: white;
}
.register:hover,
.register:active {
  color: blue;
}

.login {
  text-decoration: none;
}
.login:link,
.login:visited {
  color: white;
}
.login:hover,
.login:active {
  color: blue;
}
.icon {
  height: 3rem;
  width: 3rem;
}
