.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 2rem;
  background-color: #22b14c;
  font-size: large;
  color: white;
}

.icon {
  width: 3rem;
  height: 3rem;
}

.navigationTitle {
  text-decoration: none;
}

.navigationTitle:link,
.navigationTitle:visited {
  color: white;
}
.navigationTitle:hover,
.navigationTitle:active {
  color: blue;
}
