.filterBox {
  height: 20rem;
  width: 12rem;
  border: 2px solid black;
  padding: 1.4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.categorieName {
  font-size: 1.4rem;
  font-weight: bold;
}

hr {
  width: 100%;
  border-top: 2px solid black;
  margin-top: -7rem;
}

.showMoreBox {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.showMoreBox span {
  font-size: 1rem;
  font-weight: 700;
  color: #22b14c;
}

.triangleSymbol {
  width: 1.2rem;
}
