.productContainer {
  width: 20rem;
}
.text h1 {
  font-size: 1.6rem;
}
.text {
  padding-bottom: 1rem;
}
.price,
.status,
.reviews,
.quantity {
  border: 2px solid black;
  padding: 0.5rem;
}
.price span,
.status span,
.reviews span,
.quantity select {
  float: right;
}

.status,
.reviews,
.quantity {
  margin-top: -2px;
}
.reviews {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.reviewsContent {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.starSymbol {
  width: 1.5rem;
  height: 1.5rem;
}
.adding {
  text-align: center;
}
.addingButton {
  background-color: #22b14c;
  padding: 0.5rem;
  border: none;
  color: white;
  width: 49.75%;
}
.whiteline {
  display: inline-block;
  width: 0.1rem;
  height: 1.2rem;
}
