.reviewContent {
  width: 25rem;
  background-color: lightgrey;
  color: white;
  padding: 2rem 1rem;
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  border-radius: 1rem;
}

.reviewText {
  background-color: white;
  color: grey;
  padding: 1rem;
  border-radius: 1rem;
}

.starSymbol {
  width: 1.5rem;
  height: 1.5rem;
}
