.reviewRating {
  width: 25rem;
  padding: 2rem 1rem;
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  border: 2px solid black;
  border-radius: 1rem;
}

.reviewComment {
  border-radius: 1rem;
  height: 5rem;
  background-color: lightgrey;
  border: none;
}
.addingButton {
  background-color: #22b14c;
  padding: 0.5rem;
  border: none;
  color: white;
  width: 100%;
}
